import React, {PureComponent} from 'react'
import {
  message,
  Button
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.inventoryPass}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  render() {
    return (
      <div className={style['table-tool']}>
        <Button type="primary" className={style.btn} onClick={() => {
          const {update, selectedRowKeys, selectedRows} = this.props
          if (selectedRowKeys.length === 0) {
            message.error('请勾选要过账的商品')
            return
          }
          update({isShowModal: true, modalTitle: '盘点过账', modalData: selectedRows})
        }}>过账</Button>
      </div>
    )
  }
}



