import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  DatePicker
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect, CsxMultipleSearch} from 'Form'
import {API_PRODUCT_GETPRODUCTKEYVALUES, API_USER_WAREHOUSELIST, API_INVENTORY_GETPURCHASERGROUPALL} from 'api'
const {RangePicker} = DatePicker

@connect(
  state => ({...state.inventoryPass, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }

  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size, userLoation} = this.props
    let reqObj = {...searchParams}
    handleGetList({searchParams: {...reqObj, page: 1, size}, userLoation})
  }
  // 重置
  reset = () => {
    const {update, userLoation} = this.props
    update({searchParams: {location: {value: userLoation.value, children: userLoation.children}}})
  }
  render() {
    const {
      searchParams: {
        location,
        purchaseGroupCode,
        productCode
      },
      userLoation
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={8}>
            <CsxSelect
              dateApi={API_USER_WAREHOUSELIST}
              label='地点'
              selectItem={{data: location ? location : {value: userLoation.value, children: userLoation.children}, key: 'warehouseCode', valueKey: ['warehouseCode', 'warehouseName']}}
              onChangeSelect={(value) => {
                this.onInputChange({location: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_INVENTORY_GETPURCHASERGROUPALL}
              label='采购组'
              // searchParams={{type: '1'}}
              selectItem={{data: purchaseGroupCode, key: 'key', valueKey: ['key', 'value']}}
              // placeholder='全部'
              onChangeSelect={(value) => {
                this.onInputChange({purchaseGroupCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxMultipleSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品编码'
              searchParamsKey='productCode'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: productCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
            />
          </Col>
          {/* <Col span={24}>
            <Row type="flex" justify="end" >
              <label style={{fontWeight: 'bold', marginBottom: '12px'}}>上次盘点过账时间：2019-07-26 12:23</label>
            </Row>
          </Col> */}
          <Col span={24}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



