import * as actionType from './actionType'
import {getData} from 'util'
import {API_INVENTORY_PASSBILLS_PAGE, API_INVENTORY_PASSBILL_CREATE} from 'api'
import moment from 'moment'

//同步
export const update = (object) => ({
  type: actionType.INVENTORYPASS_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.INVENTORYPASS_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  if (!reqObj.location) {
    reqObj.location = reqData.userLoation && reqData.userLoation.value
  }
  getData({api: API_INVENTORY_PASSBILLS_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.INVENTORYPASS_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        selectedRowKeys: [], // 选中的队列key
        selectedRows: [], // 选中的队列数据
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.INVENTORYPASS_FILE
    })
  })
}

// 过账
export const handleEdit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.INVENTORYPASS_UPDATE, data: {modalTuttonLoading: true}})
  let api = API_INVENTORY_PASSBILL_CREATE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.INVENTORYPASS_UPDATE,
      data: {
        modalTuttonLoading: false,
        isShowModal: false
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.INVENTORYPASS_FILE
    })
  })
}



