import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Input,
  Form,
  DatePicker,
  Button,
  message
} from 'antd'
import moment from 'moment'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const FormItem = Form.Item

@connect(
  state => ({...state.inventoryPass, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {
    // 数据初始化
    const {modalData, update} = this.props
    if (modalData) {
      let reqObj = {location: modalData[0].locationCode},
        locationCode = []
      modalData.map((o, i) => {
        locationCode.push(`${o.locationCode} ${o.locationName}`)
      })
      reqObj.locationCode = [...new Set(locationCode)].join()
      update({modalData: {...reqObj}})
    }
  }
  handleSubmit = (e) => {
    const {form, modalData, handleEdit, selectedRows, searchParams: {initDate}} = this.props
    console.log(selectedRows, 333)
    let selectArray = []
    selectedRows.map(o => selectArray.push(o.productCode))
    e.preventDefault()
    this.setState({
      loading: true
    })
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let reqObj = {
          productCode: selectArray,
          location: modalData.location,
          passDate: moment(values.passDate).format('YYYY-MM-DD')
        }
        let result = {
            data: reqObj
          },
          msg = '操作成功'
        handleEdit({...result}, () => {
          message.success(msg)
          const {handleGetList, searchParams, page, size} = this.props
          handleGetList({searchParams: {page, size, ...searchParams}})
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowModal: false})
  }
  render() {
    const {modalTitle, form: {getFieldDecorator}, modalTuttonLoading, modalData, searchParams: {initDate}} = this.props
    const formItemLayout = {
      labelCol: {
        xs: {span: 24},
        sm: {span: 8}
      },
      wrapperCol: {
        xs: {span: 24},
        sm: {span: 16}
      }
    }
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
        width={777}
      >
        <Form onSubmit={this.handleSubmit}>
          <Row gutter={16}>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="地点"
              >
                {getFieldDecorator('locationCode', {
                  initialValue: modalData.locationCode
                })(
                  <Input disabled />
                )}
              </FormItem>
            </Col>
            {/* <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="凭证日期"
                hasFeedback
              >
                {getFieldDecorator('voucherDate', {
                  initialValue: modalData.voucherDate,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <DatePicker style={{width: '100%'}}/>
                )}
              </FormItem>
            </Col> */}
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="过账日期"
                hasFeedback
              >
                {getFieldDecorator('passDate', {
                  initialValue: modalData.passDate,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  // <DatePicker style={{width:'100%'}} onChange/>

                  <DatePicker
                    style={{width: '100%'}}
                    disabledDate={(current) => {
                      let n = new Date()
                      console.log(moment(current).year())
                      console.log(n.getFullYear(), 222)
                      return current && moment(current).month() + moment(current).year() * 12 > n.getMonth() + n.getFullYear() * 12
                    }}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row
            type="flex"
            justify="center"
          >
            <Col>
              <Button style={{marginRight: '48px'}} onClick={this.handleCloseModal}>取消</Button>
              <Button type="primary" htmlType="submit" loading={modalTuttonLoading}>保存</Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



